<template>
    <div class="lessonPrice">
        <div class="header">
            <span @click="back" class="back">
                <img src="../../assets/img/back.png" class="releaseIcon">
            </span>
            <span class="usersTitle">私教</span>
            <span class="back"></span>
            <span class="next" @click="next">完成</span>
        </div>
        <div class="price">
            价格：
            <input class="priceInput" type="tel" v-model="price">
            元/0.5小时
        </div>
    </div>
</template>

<script>
import { getLessonPrice, setLessonPrice } from '../../api/getdata'
import { Toast } from 'mint-ui'
export default {
  name: 'lessonPrice',
  data () {
    return {
      price: ''
    }
  },
  mounted () {
    getLessonPrice().then(res => {
      console.log(res)
      this.price = res.data
    })
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    next () {
      if (this.price !== '') {
        let data = setLessonPrice({'PersonalTrainingPrice': this.price})
        data.then(res => {
          console.log(res)
          if (res.status === '0') {
            Toast({
              message: '设置价格成功',
              duration: 1500
            })
            this.$router.go(-1)
          }
        })
      } else {
        Toast({
          message: '请先填写价格',
          duration: 1500
        })
      }
    }
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    font-size: 0.48rem;
  }

  .header {
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: relative;
    font-size: 0.48rem;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  .next {
    position: absolute;
    right: 0;
  }

  img {
    width: 100%;
  }

  .price {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
    color: #333333;
    text-align: start;
    margin-top: 0.2rem;
  }

  .priceInput {
    outline: none;
    border: none;
    border-bottom: 2px solid #f5f5f5;
    width: 2rem;
    text-align: center;
  }
</style>



